.login-error {
    color: white;
    position: absolute;
    top: 5%;
    left: 50%;
    font-weight: bold;
    border-radius: 10px;
    background-color: var(--dark-accent);
    padding: 1em;
    width: 80%;
    -webkit-transform: translate(-50%, -10%);
            transform: translate(-50%, -10%);
    opacity: 0;
    -webkit-animation: error 0.2s ease forwards;
            animation: error 0.2s ease forwards;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
}

@-webkit-keyframes error {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-50%, -10%);
                transform: translateY(-50%, -10%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translate(-50%, 0);
                transform: translate(-50%, 0);
    }
}

@keyframes error {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-50%, -10%);
                transform: translateY(-50%, -10%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translate(-50%, 0);
                transform: translate(-50%, 0);
    }
}

.signup-success {
    color: var(--dark-shade);
    position: absolute;
    top: 5%;
    left: 50%;
    font-weight: bold;
    border-radius: 10px;
    background-color: var(--light-accent);
    padding: 1em;
    width: 80%;
    -webkit-transform: translate(-50%, -10%);
